  <div class="announce">
    <h1>2020第三届中国文化计算大会通知</h1>
      <p>各有关单位：</p>
      <p>2020第三届中国文化计算大会（2020 The Culture Computing Conference of China，简称CCCC）将于2020年10月22日至25日在厦门大学举办。会议将由中国通信学会主办，厦门大学和中国通信学会移动媒体与文化计算委员会承办，北京邮电大学、中国虚拟现实与可视化产业技术创新战略联盟、福建省通信学会、中国人工智能学会智能创意与数字艺术专业委员会、厦门市语言与文化计算重点实验室等多家单位共同协办，同时由西南大学计算机与信息科学学院承办2020第三届中国文化计算大会—民族文化数字化保护与传播分论坛。</p>
      <p>文化计算是将社会计算、大数据、人工智能等技术与人文、历史等学科相互交叉融合，实现文化内容挖掘传播、数字人文研究，促进文化繁荣发展最有力的技术手段。中国文化计算大会自2018年创办以来每年召开一次，迄今为止已经成功举办了两届。本次大会将继续向国内从事文化计算相关研究的科技、文化、产业专家学者征集优秀论文，开展研讨与交流，分享科技最新研究动态。</p>
      <p> 通过本次大会在科技、文化与产业界建立起一座文化计算相关研究的桥梁，将科技的发展与文化产业的需求进行沟通与交流。构建这一领域的研究联盟，探索与推动文化计算相关研究的重点项目立项，通过科技与文化产业的深度融合推动文化产业的发展。</p>
      <p>诚挚欢迎全国各科研院所、相关产业研究与应用单位的广大工作者参加本届大会。</p>
