import Vue from "vue";
import VueRouter from "vue-router";
import MeetingAnnouncement from "../components/MeetingAnnouncement";
import ReportContent from "../components/ReportContent";
import CallPapers from "../components/CallPapers";
import Register from "../components/Register";
import Schedule from "../components/Schedule";
import TransportationAccommodation from "../components/TransportationAccommodation";
import InvitedReport from "../components/InvitedReport"
import CulturalReport from "../components/CulturalReport";
import ExperienceReport from "../components/ExperienceReport";
import PromotionReport from "../components/PromotionReport";
import Organization from "../components/Organization"
Vue.use(VueRouter);

const routes = [
  {
    path:'/',
    redirect:'/MeetingAnnouncement'
  },
  {
    path: '/MeetingAnnouncement',
    name: "会议通知",
    component: MeetingAnnouncement,
  },
  {
    path: '/ReportContent',
    name: "会议报告简介",
    component: ReportContent,
  },
  {
    path: '/CallPapers',
    name: "大会征文",
    component: CallPapers,
  },
  {
    path: '/Register',
    name: "大会注册",
    component: Register,
  },
  {
    path: '/Schedule',
    name: "会议日程",
    component: Schedule,
  },
  {
    path: '/TransportationAccommodation',
    name: "交通住宿",
    component: TransportationAccommodation,
  },
  {
    path: '/ReportContent/InvitedReport',
    name: "特邀报告",
    component: InvitedReport,
  },
  {
    path: '/ReportContent/CulturalReport',
    name: "文化报告",
    component: CulturalReport,
  },
  {
    path: '/ReportContent/ExperienceReport',
    name: "体验报告",
    component: ExperienceReport,
  },
  {
    path: '/ReportContent/PromotionReport',
    name: "推广报告",
    component: PromotionReport,
  },
  {
    path: '/Organization',
    name: "推广报告",
    component: Organization,
  },
];

const router = new VueRouter({
  routes
});

export default router;
