var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"report"},[_c('h1',[_vm._v("民俗与非遗文化报告")]),_c('hr'),_c('b',[_vm._v("特邀讲者：米海鹏，清华大学")]),_c('br'),_c('br'),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"display":"inline-block","height":"auto","max-width":"100%"},attrs:{"src":require("@/assets/images/3/31.png")}})]),_c('br'),_c('b',[_vm._v("报告主题：“墨甲”中国风机器人乐队：当文化计算遇上艺术机器人")]),_c('br'),_c('b',[_vm._v("时间地点：")]),_vm._v("2020年10月24日，周六，9:00-9:30，科艺中心会议室4。"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("机器人艺术是一个新兴的学科交叉领域。在探索机器人的艺术表现形式时，文化因素是不应该被忽视的。“墨甲”机器人乐队最早诞生于清华大学，是一次将中国传统文化与机器人艺术相结合的大胆尝试。这支乐队的三名机器人乐手可以分别演奏三种中国传统乐器。墨甲中国风机器人乐队的设计，运用了多个学科的技术和方法。我们构建了一套数字孪生环境，用于快速地设计多样的机器人角色，以及更加复杂的演出场景。而基于动作捕捉驱动机器人的技术，让乐队的新成员具备了丰富的面部表情表现能力。新的计算技术和方法，让中国风机器人艺术具有了更多的可能性。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("米海鹏，博士，副教授，2017年入选中组部“万人计划”青年拔尖人才，同年荣获清华大学“学术新人奖”。现任教于清华大学美术学院信息艺术设计系，并兼任清华大学未来实验室客座研究员。作为一名跨学科设计领域的青年学者，他拥有多学科的教育背景，以及丰富的跨学科设计研究经历。其多年的设计科研工作致力于探索融合艺术与科学、设计与科技的前瞻性和挑战性的研究课题，是一名勇于实践的创新者。")]),_c('p',[_vm._v("近年来，米海鹏博士致力于探索中国传统文化、传统技艺与新兴交互技术相结合的新的设计可能性。他率先提出了中国风机器人艺术的概念，将中国传统文化与艺术元素与先进的机器人技术相结合，创造了世界上第一支机器人乐队“墨甲”。这一研究工作经过大量媒体报道，在社会上引起广泛关注，曾两次登上热搜，在网上阅读量超过1.2亿次。")]),_c('p',[_vm._v("米海鹏分别于2005年和2008年分别于清华大学获得了理学学士（物理）及工学硕士（电子科学与技术）学位，并于2011年于东京大学获得工学博士（电子信息系统）学位。加入清华美院之前，他曾历任日本科学技术振兴机构研究员、研究推进委员，以及东京大学助理教授等职。")]),_c('hr'),_c('br'),_c('br'),_c('br'),_c('hr'),_c('b',[_vm._v("特邀讲者：郑国珍，中国文物学会、福建省文物局")]),_c('br'),_c('br'),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"display":"inline-block","height":"auto","max-width":"100%"},attrs:{"src":require("@/assets/images/3/32.png")}})]),_c('br'),_c('b',[_vm._v("报告主题：福建省文化遗产")]),_c('br'),_c('b',[_vm._v("时间地点：")]),_vm._v("2020年10月24日，周六，9:30-10:00，科艺中心会议室4。"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("本报告将就福建省文化遗产的状况做梳理和介绍，具体包括三个部分：世界遗产在福建；国家历史文化名城、名镇、名村与传统村落在福建；全国重点文物保护单位在福建。其中，世界遗产在福建部分还将进一步从五个方面展开：世界文化遗产、自然遗产、文化和自然遗产在福建；世界灌溉工程遗产在福建；全球重要农业文化遗产在福建；世界非物质文化遗产在福建；中国世界遗产预备对象在福建。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("郑国珍，曾任福建省文化厅党组成员、福建省文物局局长，福建省文博系列正高级职称评审委员会主任委员，厦门大学人文学院兼职考古学教授，福建师范大学特聘教授等。现任中国文物学会副会长及古村镇专业委员会主任委员、历史文化街区专业委员会副主任委员，国家文物局文化遗产专家组成员、工程管理专家组成员，国际古迹遗址理事会国际会员，福建省文史研究馆馆员及咨政研究院副院长，福建省理论宣讲专家库首批专家，福建省高校思想政治理论课特聘教授，福建省直机关党工委讲师团特聘教授，福建省历史文化名镇名村和传统村落专家委员会主任，福建省历史文化名城名镇名村与历史文化街区研究中心学术委员会主任，福建省海洋文化研究中心研究员，福建省文物考古博物馆学会会长等。")]),_c('p',[_vm._v("主要研究领域涉及：文物考古博物馆学与文化遗产的保护传承、传统街区和名城名镇名村的保护发展等方面理论探索与实践总结。主持完成《中心城市的传统街区保护》《历史文化名镇（乡）、名村保护现状与发展对策》等国家文物保护科学和技术研究课题；主持编撰出版《福建大观·福建文物》、《中国文物地图集·福建卷》（上下）、《中国音乐大系·福建卷》、《福建省志·文物志》、《福建土楼》、《福建三明万寿岩旧石器时代遗址》、《福建涉台文物图录》（上下）、《福建省革命文物图册》、《顺昌元坑古镇》等数十部著作；撰写发表《闽越文字考》《“永隆通宝”铁钱产生的历史背景及其制作工艺初探》《寻觅历史的踪迹－跨世纪的福建考古》《闽南史前文化初探》《海峡两岸城隍信仰在神缘关系中的地位等近百篇论文；主持编写的20余部微电影剧本中，《刺桐城标•东西塔》获第三届亚洲微电影艺术节“优秀作品奖”和“最佳摄影奖”，第四届华东六省一市暨部分城市微电影（微视频）大赛纪实类一等奖；《魏征•刚直不阿斗权贵》获第四届亚洲微电影艺术节“金海棠奖”；《黄君奕住•厦鼓甘露》和《胡友义的钢琴天堂》获国家广电总局2015全国原创网络视听节目优秀作品；《致我们终将逝去的爱情》获国家广电总局2016年全国原创网络视听节目优秀网络电影；《越过梦山》获2017年福建省第八届百花文艺奖。先后参与主持将：中国“武夷山”作为世界文化与自然遗产，将中国“福建土楼” 、中国厦门“鼓浪屿：历史国际社区”作为世界文化遗产，分别申报列入到《世界遗产名录》；将中国“泉州（刺桐）史迹”（现更名“泉州：宋元中国世界海洋商贸中心”）申报世界文化遗产；将“海上丝绸之路――福州、泉州、漳州史迹”、福州“三坊七巷”、闽南“红砖建筑”、闽东北“木拱廊桥”申报列入到《中国世界文化遗产预备名录》；将福建省域的历史文化名镇名村、历史文化街区、传统村落、文物保护单位申报进入到国家级保护系列等。")]),_c('hr'),_c('br'),_c('br'),_c('br'),_c('hr'),_c('b',[_vm._v("特邀讲者：佘莹莹，厦门大学")]),_c('br'),_c('br'),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"display":"inline-block","height":"auto","max-width":"100%"},attrs:{"src":require("@/assets/images/3/33.png")}})]),_c('br'),_c('b',[_vm._v("报告主题：赋能文旅，点亮非遗：提线与掌中木偶表演机器人")]),_c('br'),_c('b',[_vm._v("时间地点：")]),_vm._v("2020年10月24日，周六，10:20-10:50，科艺中心会议室4。"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("珍贵的文化遗产是我们祖先智慧、审美、生活情趣的见证，也是中华优秀传统文化的代表。提线与掌中木偶机器人新媒体交互表演与推广项目是传统闽南木偶表演与跨媒体艺术、现代科技的结合。项目秉承了“非遗保护与传承”与“艺术创新与推广”的理念，基于“以人为本”的核心思想，针对闽南木偶及其发展现状进行了充分的实地考察与调研，尝试利用现代技术，来解决制约传统木偶艺术表演进行传播推广的瓶颈问题，实现木偶艺术与现代科学技术的深度融合。")]),_c('p',[_vm._v("项目立足于非遗文化，以及孕育其发展的悠久闽南历史与文化，通过自主研发的木偶表演机器人以及互动媒体艺术、互联网等多学科交叉手段，让观众不仅能够融入到虚实结合的木偶表演现场，还能够获得沉浸式体验，感受古代丝绸之路以及闽南文化的多元魅力。交互表演以闽南古建筑作为舞台，基于木偶传统演出剧目，开创性地构建了机器人、演员与观众三者互动的表演模式。这样新颖的多元化表演模式，有效地吸引国内外各年龄层观众，让公众产生价值认同和情感共鸣。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("佘莹莹，博士，副教授，现任教于厦门大学信息学院软件工程系。近年来，佘莹莹博士致力于非遗文化的科技创新之路，探索跨媒体艺术与智能在非遗传承与发展中所起的作用。她与来自于信息、艺术、人文、非遗传承人等组成跨学科团队研发的世界首创的提线木偶与掌中木偶表演机器人，将机器人技术与闽南传统非遗表演进行融合。团队与非遗木偶表演联合不仅前往美国、日本、新加坡等国家，也进入大、中、小学校，将最传统的掌中木偶文化艺术与最前瞻的科技创新展示给国内外的青少年，引发人们的文化共鸣。这一研究工作获得了中国国家艺术基金的资助，也吸引了大量海内外媒体报道，获得了多项国家级创新奖励，并于2020年度获得中国文化部“文化和旅游信息化发展典型案例”。")]),_c('p',[_vm._v("佘莹莹于2006年和2011年分别于加拿大Concordia大学获得了计算机科学硕士及哲学博士（计算机科学）学位，并于2011年进入厦门大学担任助理教授、副教授至今。")]),_c('hr'),_c('br'),_c('br'),_c('br'),_c('hr'),_c('b',[_vm._v("特邀讲者：祁春英，中央民族大学")]),_c('br'),_c('br'),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"display":"inline-block","height":"auto","max-width":"100%"},attrs:{"src":require("@/assets/images/3/34.png")}})]),_c('br'),_c('b',[_vm._v("报告主题：激活传统、创新未来一一中国少数民族传统服饰图案解读")]),_c('br'),_c('b',[_vm._v("时间地点：")]),_vm._v("2020年10月24日，周六，10:50-11:20，科艺中心会议室4。"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("中国少数民族服饰有着一山不同天，一里不同服的说法各民族服饰千姿百态的款式、巧夺天工的技艺，尤其寓意深刻的服饰传统图案将各民族的历史、宗教、迁徒路线、图腾崇拜等把服饰文化内涵体现的淋漓尽致！而且古朴、独特，凝聚着民族的精神与情感！在国家当下非物质文化遗产保护中，时尚服饰的创新设计中都有着十分重要的历史意义！")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("祁春英，本科、硕士分别毕业于中央民族大学历史系民族史及专门史专业，现任中央民族大学少数民族服饰研究所所长，研究员，硕士生导师。")]),_c('p',[_vm._v("一、主要业绩：2002年11月在中国国际时装周《民族魂——少数民族服饰专场发布会》荣获组委会颁发的“中国服饰文化贡献奖”；2009、2019年分别担任中华人民共和国“国庆六十周年、七十周年大庆”民族服饰设计、制作艺术总监。并获得游行指挥部颁发的“突出贡献专家”荣誉证书；2011年5月担任西双版纳傣族大型服饰晚会“穿在身上的历史”总导演和服饰品牌“水韵.娑罗”创始人；2013年担任青海省海南藏族自治州六十周年庆典《轮回中的印记》总导演；2016年起担任文化部 “锦绣中华---中国非遗服饰秀”专家委员会副主任；2018年-2021年被光明日报聘请为“中国非遗大使”推广专家委员会专家；2018年被北京服装学院聘为“中国传统工艺振兴计划协同创新中心传统服装服饰研究所所长”；2018年荣获中纺业联 “首创杯”2018年度这个纺织非遗推广大使；2019年被聘为“全国少数民族服饰传承保护标准化技术委员会”专家委员；2019年被福建省文化厅聘请为（2019.9-2024.9）福建省服装创意设计中心第一届学术委员会委员。")]),_c('p',[_vm._v("二、出版的主要著作：专著《中国少数民族头饰》；专著《中国少数民族婚俗》；专著《中国少数民族服饰文化研究》；专著《中国最美——民族服饰》")]),_c('p',[_vm._v("三、主要国际文化交流：2000年11月“中国文化美国行”，2001年11月“中国文化德国行”活动中分别担任少数民族服饰设计、制作与总顾问；2003年9月前往匈牙利佩奇镇进行民族服饰文化交流；2006年7月前往韩国女子学院，进行中韩服装服饰文化交流；2011年9月前往日本松山嫒圆大学进行中国少数民族服饰展览及文化交流；2013年9月应国家民族事务委员会邀请，前往波兰参加中国波兰文化周活动。和华沙民俗博物馆进行民族服饰文化交流及讲座；2018年9月应文化部、国家民委文化交流项目前往柬埔寨金边作\"羌族服饰”的文化交流。")]),_c('hr'),_c('br'),_c('br'),_c('br'),_c('hr'),_c('b',[_vm._v("特邀讲者：李凯勇，青海民族大学")]),_c('br'),_c('br'),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"display":"inline-block","height":"auto","max-width":"100%"},attrs:{"src":require("@/assets/images/3/35.png")}})]),_c('br'),_c('b',[_vm._v("报告主题：面向青绣文化保护与传承的创意设计方法研究")]),_c('br'),_c('b',[_vm._v("时间地点：")]),_vm._v("2020年10月24日，周六，11:20-11:50，科艺中心会议室4。"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("“青绣”作为宣传青海的新名片，汇集了青海地区多个少数民族非物质文化遗产的结晶，成为青海最具代表性的文化符号。现有的青绣数字化保护以数字采集为主，缺乏元素提取、构图分析等研究，“只看不用”成了阻碍青绣产业发展的主要瓶颈；局限于传统题材，设计过于陈旧，难以适应新时代的发展理念和大众的个性化需求；图案设计严重依赖非遗传人的经验，但又缺乏针对青绣的计算机辅助设计方法，通用设计软件效率低、智能化程度较差，无法支持青绣的创意设计。如何促进非物质文化遗产的不断传承与持续性发展、探索中国特色文化遗产传承利用创新服务模式，具有维护文化多样性的固有功能和促进经济发展的工具性作用两方面的发展价值。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("李凯勇，长期从事文化计算和人工智能方面技术与非物质文化遗产的数字化研究工作，已初步完成青海“堆绣”图案的数据挖掘图像特征分割、大区域图像局部破损点优化提取、监督性机器学习算法的图像辅助去噪、机器学习的光照不均图像边缘检测系统等相关前期工作。")]),_c('hr'),_c('br'),_c('br'),_c('br')])}]

export { render, staticRenderFns }