var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"report"},[_c('h1',[_vm._v("文化计算与文化体验报告")]),_c('hr'),_c('b',[_vm._v("特邀讲者：赵海英，北京邮电大学")]),_c('br'),_c('br'),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"display":"inline-block","height":"auto","max-width":"100%"},attrs:{"src":require("@/assets/images/2/21.png")}})]),_c('br'),_c('b',[_vm._v("报告主题：文化计算在国家文化大数据体系建设中的实践与思考")]),_c('br'),_c('b',[_vm._v("时间地点：")]),_vm._v("2020年10月23日，周五，14:30-15:00，科艺中心会议室4。"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("随着现代信息和数字技术的发展，一门新的研究学科文化计算随之诞生。文化计算是利用社会计算、大数据、人工智能等技术与人文、历史等学科相互交叉融合，实现文化内容挖掘传播、推动数字人文研究，促进文化繁荣发展的技术手段。文化计算。2020年5月中宣部文改办下发的《关于做好国家文化大数据体系建设工作通知》中，提出建设文化大数据体系的八大任务，涉及到了数据的采集、存储、传输和应用，同时明确了文化大数据的中国文化遗产标本库、中华民族文化基因库以及中华文化素材库的建设，这些都体现了国家的意志。")]),_c('p',[_vm._v("文化计算离不开可计算的数据支撑。国家文化大数据体系建设就是要营造一种全社会共同推进文化发展的气氛，形成文化产业发展的循环经济。文化产业需要工具，更需要灵魂。文化计算一方面是文化产业的工具，从文化基因溯源看，文化计算也是文化产业发展的灵魂，通过文化计算的实践引申出新思考。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("赵海英，控制工程与科学专业博士学位，移动媒体与文化计算北京市重点实验室主任，文化计算与文化遗产数字化保护方向学术带头人。中国通信学会移动媒体与文化计算专业委员会秘书长，中国工业与应用数学学会几何设计与计算专委会委员、中国ACMSIGCHI 学会专业委员。首次完整提出文化基因的概念：是在特定地域和文化环境中形成的具有稳定性和继承性的基本信息模式，蕴含着文化固有的本质属性，并演绎着文化时空规律。总结了文化基因的三大特征，建立了文化基因的特征量化空间，为文化基因可量化、可计算、可分析提供理论依据。正在搭建文化基因语义标签网PatternNet，开展文化资源数字化理论与传承机理探寻的基础研究。")]),_c('hr'),_c('br'),_c('br'),_c('br'),_c('hr'),_c('b',[_vm._v("特邀讲者：沈贵华，中国文物信息咨询中心")]),_c('br'),_c('br'),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"display":"inline-block","height":"auto","max-width":"100%"},attrs:{"src":require("@/assets/images/2/22.png")}})]),_c('br'),_c('b',[_vm._v("报告主题：文化计算赋能文化遗产价值挖掘与传承")]),_c('br'),_c('b',[_vm._v("时间地点：")]),_vm._v("2020年10月23日，周五，15:00-15:30，科艺中心会议室4。"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("中华文明博大精深，文物瑰宝灿若繁星，是历史中国源远流长的实物见证。文物蕴含着重要的历史、艺术和科学价值，让历史文物资源活起来，让文化网络空间亮起来，让民族精神内核传下去，是习近平总书记关心的大事；党的十八大以来，文博行业把握数字化时代重大机遇，部署文物资源数字化、文物管理信息化、文物展示网络化；面对人民群众日益高涨的精神需求，把计算机相关技术应用到文化遗产领域，探索其中的发展规律，揭示其内在关联并对其进行可视分析与呈现传播，有助于挖掘文化遗产内容的本质和传承优秀文化，讲清楚文化遗产蕴藏的历史传统和文化积淀，讲清楚中华民族独特的价值体系和精神追求，让文物成为民族认同、文化认同、国家认同的重要精神桥梁。借助文物数字互联，打通文物与科技、文物与民生、文物与教育、文物与旅游等行业的深度融合渠道，服务国家战略大局，助力社会繁荣进步。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("沈贵华，中国文物信息咨询中心政务信息部主任，中国文物学会理事、中国文物学会信息化专业委员会副主任委员。主要从事国家文物局电子政务、国家文物资源数据库、文物博物馆信息化等领域研究与实践。承担国家文物局电子政务平台规划、建设与运维；建设并运行国家文物资源数据库，为全国馆藏珍贵文物调查、第三次全国文物普查、第一次全国可移动文物普查等国家级重点文物数字化项目提供技术保障；承担部分省局及博物馆信息化规划编制工作，联合北京、山东、吉林、四川等省开展省域博物馆公众服务与博物馆大数据平台建设，推动普查数据公开共享、文物数字化传播，开展文物知识服务应用实践；在行业信息化总体规划、标准规范、平台建设、智慧博物馆与数字化保护等方面积累了丰富的经验。")]),_c('hr'),_c('br'),_c('br'),_c('br'),_c('hr'),_c('b',[_vm._v("特邀讲者：桑新柱，北京邮电大学")]),_c('br'),_c('br'),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"display":"inline-block","height":"auto","max-width":"100%"},attrs:{"src":require("@/assets/images/2/23.png")}})]),_c('br'),_c('b',[_vm._v("报告主题：基于裸眼3D光场显示的文创体验")]),_c('br'),_c('b',[_vm._v("时间地点：")]),_vm._v("2020年10月23日，周五，15:50-16:20，科艺中心会议室4。"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("为了增强文化产品的创新体验，研究基于数字3D光场构建的裸眼3D光场显示交互呈现方式。模拟真实世界中人眼观看中物体或场景的呈现方式，是实现裸眼3D光场显示基本途径。介绍裸眼3D光场显示的基本原理，讨论裸眼3D光场显示的特点和基本实现方法，结合文化和数字化文物的3D光场创新呈现体验，给出了北京邮电大学在大尺寸裸眼3D光场显示方面开展的一些研究工作。实现了1.8m ×1.2 m幅面的光场显示，显示深度超过1m；实现了27英寸9216个视点光场信息的可交互全视差3D光场显示工作，水平和垂直视角45度；实现了65英寸的超过100度视角的3D光场显示，具有正确的空间危机几何遮挡关系;实现了基于LED的54英寸和162英寸的大尺寸、大视角的裸眼3D光场显示;实现了120°宽视角的裸眼3D光场显示和2m×2m、分辨率为23040×20650、视角为80°的裸眼3D光场显示。通过构建自由空间3D光场分布和高效计算，实现了多种形态自由空间空气成像的高清晰度动态裸眼3D光场显示。文物和文创产品的裸眼3D数字化光场呈现和交互，可提升人们对文物和文创作品的了解、认识和兴趣，有助于文化传承和宣传。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("桑新柱，北京邮电大学电子工程学院教授，博士生导师，副院长；信息光子学与光通信国家重点实验室研究三维显示与光电信息处理团队责任教授。2011年入选北京市科技新星计划和教育部新世纪优秀人才支持计划，2016年获得“北京市师德先锋”称号，2017年被评为北京市优秀教师，2018年被评为中国电子学会先进科技工作者，2019年获得教育部技术发明二等奖（排名第1）。兼任工信部电子科技委委员、中国光学学会理事、中国光学学会全息与光信息处理专业委员会副主任委员和秘书长、中国电子学会虚拟/增强现实技术与产业分会副主任委员、北京光学学会光学成像专委会主任委员等。")]),_c('p',[_vm._v("主要从事裸眼3D光场显示、全息显示级应用方面的研究工作。作为项目负责人承担国家级和省部级项目30余项，和20多家单位开展了应用合作，部分成果实现了产业转化，发表学术论文200余篇（被引用1500多次）；申请发明专利100多项，授权发明专利51项（转让2项，实施许可7项）；获得软件著作权11项。 ")]),_c('hr'),_c('br'),_c('br'),_c('br'),_c('hr'),_c('b',[_vm._v("特邀讲者：林俊廷，青鸟新媒体艺术")]),_c('br'),_c('br'),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"display":"inline-block","height":"auto","max-width":"100%"},attrs:{"src":require("@/assets/images/2/24.png")}})]),_c('br'),_c('b',[_vm._v("报告主题：科技为文化插上翅膀，文化为科技增添温度：5G时代下的科技艺术")]),_c('br'),_c('b',[_vm._v("时间地点：")]),_vm._v("2020年10月23日，周五，16:20-16:50，科艺中心会议室4。"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("以文化为本源，以科技为手段，以在建筑、星宿、器玩、戏曲、园林等处处可见的“故宫神兽”身影和故事为主线，内容涵盖中国传统自然观、哲学观、生活方式等，结合当前蓬勃发展的5G通讯、大数据、云计算、物联网、人工智能等前沿技术且受年轻群族追捧的科技手段、沉浸式互动场景体验，用科技艺术唤醒更多年轻国人喜爱传统文化，同时打开不同文明交流的一扇窗，成为新时代中国“文化与科技”的国家名片。 文化和科技深度结合，既催生了新的文化业态、延伸了文化产业链，加速优秀原创的科技艺术内容生产和激发文化创造活力，助力传统文化产业转型升级。同时，又集聚了大量创新人才，将加速通讯、计算机、人工智能等科技人才与人文、艺术、影视等跨学科人才的融合和复合型人才的培养，使科技、文化和商业不再割裂。引领数字文化产业迎来新一轮爆发式增长，培育数字经济新优势，重塑数字技术的文化产业新生态，形成更强大的国家创新活力，触发新一轮经济增长周期。 依托深厚的中华优秀传统文化底蕴和与日俱增的国际科技竞争力，推动社会资源更广泛的融入国家公共文化和先进科技建设，通过“坚守工匠精神，赋能科技温度，引领消费升级”的方式探索科技艺术成为社会发展的新引擎。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("林俊廷，新媒体艺术家，生于台湾·台北，定居上海，现任青鸟新媒体艺术-艺术总监，四川美术学院新媒体艺术系特聘教授、硕士研究生导师，是引领当代华人新媒体艺术风潮的代表性人物。其擅长透过物理三维空间和第四维「时间」的交融呈现，让观者经由身体参与、经历心理知觉变化而完成一种「幻境体验」，在进入现实与非现实的缝隙之际，当下的感知与如梦的体验交互辩证构成一种完形审美经验。曾获美国洛克斐勒基金会ACC亚洲文化协会国巨科技艺术奖，为第一位在纽约Location One科技艺术中心驻村创作的台湾艺术家，作品曾应邀于英国、法国、意大利、芬兰、美国、日本、韩国、新加坡等国际展览中展出，并获美国、澳洲及新西兰等地的美术馆和上海文化发展基金会、国巨基金会……等永久收藏。")]),_c('p',[_vm._v("林俊廷作品以光与影交织的诗意风格和人文气息，藉由和潜意识世界的和谐对话，让人们进入异度的时空漫游，古典轻盈、实拟虚境的手法语汇，激发出人类所共有的视觉记忆或某些潜意识向度，美好神秘的心灵彼岸意象油然再现。2002年，成立青鸟新媒体艺术——一个以光和影造梦的创意实验室，邀集来自视觉艺术、博物馆展示、文学、游戏创意开发、动画、程序、电子电路等不同专业的创意人，专注以当代新媒体重新转译中国文化与美学，不断跨界创作发展新的概念与呈现方式。2011年，与台北故宫博物院合作「山水觉─黄公望与富春山居图新媒体艺术特展」，由不同角度切入诠释《富春山居图》之神思意境，是目前亚洲唯一展览荣获美国博物馆协会(AAM)《谬思奖》MUSE诠释互动金奖(2012)、英国艺术报(The Art Newspaper)评选为当年度全球人气最高的展览第三名。2020年，故宫博物院建成600年之际，林俊廷及青鸟新媒体艺术更将与其携手，以新媒体艺术手法，转化中华民族深厚的传统文化，诠释属于华人的真正内涵，创造属于新时代的文化中国梦。")]),_c('hr'),_c('br'),_c('br'),_c('br'),_c('hr'),_c('b',[_vm._v("特邀讲者：王海燕，四川美术学院")]),_c('br'),_c('br'),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"display":"inline-block","height":"auto","max-width":"100%"},attrs:{"src":require("@/assets/images/2/25.png")}})]),_c('br'),_c('b',[_vm._v("报告主题：大足石刻数字化保护研究")]),_c('br'),_c('b',[_vm._v("时间地点：")]),_vm._v("2020年10月23日，周五，16:50-17:20，科艺中心会议室4。"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("主要利用深度卷积神经网络、数字图像修复算法、图像融合技术、图像学、美术史学与考古学方法，交叉应用于大足石刻的数字图像识别、虚拟修复与美术考古研究。将计算机领域的定量研究方法应于石窟艺术研究，一方面印证传统的定性研究的合理性，另一方面也对一些传统研究方法无法解决的问题做出有益探索。研究首先提出一种有效的基于VGGNet的大足石刻佛教头像的造型风格识别方法，并对北山180窟观音造像的数量作出辨析；然后改进基于样本的图像修复算法，提出基于样本库生成样本图以完成图像修复的方案，并将之应用于石门山6号窟杨柳观音头像的虚拟修复，在一定程度上可以解决当原图像因破损区域太复杂或过大而无法提供充足先验信息的艺术图像修复问题。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("王海燕，计算机应用方向博士，四川美术学院艺术人文学院教师，硕士研究生导师。研究方向：影像艺术、文化遗产数字化保护。主要致力于大足石刻数字图像识别与虚拟修复研究，主要研究成果有：出版学术专著1部，发表SCI及A&HCI检索、CSSCI检索、北大核心及其他重要学术期刊论文10余篇，主持完成与在建省部级重点项目2项、一般项目2项。")]),_c('hr'),_c('br'),_c('br'),_c('br')])}]

export { render, staticRenderFns }