  <h1>2020年第三届中国文化计算大会 征文通知</h1>
  <p>文化计算（Culture Computing）是采用社会计算、大数据、人工智能等技术促进文化内容的挖掘与传播，推动文化繁荣发展的新兴研究领域。文化计算属《国家文化科技创新工程纲要》所提及的共性关键技术，通过科技创新带动文化创新是当前的国家战略。为积极探讨和推动文化计算领域的学术交流和产业发展，定于2020年10月22日至25日在厦门大学举办“第三届中国文化计算大会（the Culture Computing Conference of China，简称CCCC）”。本次大会由中国通信学会主办，厦门大学和中国通信学会移动媒体与文化计算委员会承办，北京邮电大学、中国虚拟现实与可视化产业技术创新战略联盟、福建省通信学会、中国人工智能学会智能创意与数字艺术专业委员会、厦门市语言与文化计算重点实验室等多家单位共同协办。</p>
  <p>本次大会将向国内从事文化计算相关研究的专家学者征集优秀论文。会议组委会将邀请来自文化计算、计算机、人工智能、语言与文化、民俗和非物质文化遗产、建筑和艺术等交叉领域的专家学者，以及文化科技领域的企业代表就文化计算的科学研究、产业发展进行研讨，报告最新研究进展，交流新的学术思想和方法，展望文化计算的未来发展趋势。</p>
  <p>论文采用中文，被录用论文的作者将被邀请到会做交流报告，录用的论文将被推荐到《图学学报》、《浙江大学学报》（理学版）、《厦门大学学报》等期刊。</p>
  <p>热烈欢迎文化计算领域的科研院所、产业与应用研发单位的人员参加本届大会。</p>
