      <b>报告主题：基于5G+脑科学+AI+智能超算的文创产业应用和人才培养</b><br/>
      <b>时间地点：</b>2020年10月23日，周五，09:10-10:00，科艺中心报告厅。<br/>
      <b>报告摘要：</b>
      <p>利用5G+脑科学+AI+智能超算制作高品质电影和5D虚拟主题公园。在10台专用、互连、分布式、并行和协作的超级计算机上制作一部3D/8K/120帧每秒的世界级数字电影；研究、开发世界上最先进的5D数字主题公园（5D-VTP）技术，利用5D特效（声音、图像、气味、触摸、运动）和第三大脑内容（在云上将人脑及其人工智能延伸进行动态耦合）增强娱乐效果，疗愈、改善和启迪从孩童到老年各个阶段人群的身心和精神，提升人们的良知、人性和道德水平，消除人类的贫穷、偏见、贪婪、仇恨、腐败和战争，加速全球合作、理解和创新，加快在全球范围内建立紧密联系、相互支持、美丽、清洁、生态友好、社会经济可持续的智慧型园林乡村或城市，维护人类与自然的和谐，维护世世代代的世界和平与繁荣。</p>
      <b>个人简介：</b>
      <p>陈世卿，美国国家工程院院士、美国艺术与科学院院士、美国第三脑研究院院长、中国智慧城市建设投资联盟首席科学家、中国四川大学锦城学院人工智能学院院长、中国深圳人工智能与机器人研究院主任研究员、中国北京清华大学脑与认知科学研究院首席科学家、中国产业发展研究院人工智能研究院首席科学家。研发了世界上第一台用于科学研究的并行向量超级计算机；研发了使用通用型64-256 CPU的企业用高性能计算机；研发了世界上第一台128个刀片的超级计算机；研发了世界上第一个以互联网为基础、应用对应用、高效率、动态式企业协同作业软件TONBU；研发了世界上第一个以超级计算机为基础，全球联网形成类似电力网格的信息网格SCP；组织美国硅谷的非营利第三脑研究院Third Brain Research Institute，专注于世界前沿关于研究脑、保护脑、发展脑和延伸脑的研发项目，并筹建安全、可靠、高效的分布式/并行式/协作式超级网格云端平台SCP用以支持全球巨量、实时、遍布、无所不在的大数据存储和模拟运算。</p>
