  <b>特邀讲者：查红彬，北京大学</b><br/><br/>
  <div style="text-align: center;"> <img src='@/assets/images/1/12.png' style=" display: inline-block;height: auto;max-width: 100%;" > </div><br/>
  <b>报告主题：“3D视觉：在线学习的途径”</b><br/>
  <b>时间地点：</b>2020年10月23日，周五，10:00-10:50，科艺中心报告厅。<br/>
  <b>报告摘要：</b>
  <p>3D视觉的一个主要任务是利用传感器视点的变化与成像几何的约束来实现三维场景的几何与结构重建。因此，伴随传感器移动的动态视觉与3D视觉的关系愈加密切，SLAM（Simultaneous Localization and Mapping：即时定位与地图构建）技术再度成为3D视觉领域的研究热点。为了提高动态视觉系统在真实复杂场景中的应用能力，我们应充分强化视觉系统的环境自适应性，而在线学习方法是实现这一目标的有效途径。该报告将围绕基于在线学习的SLAM问题，介绍我们近来的一些想法和尝试，主要内容包括：面向自监督视觉里程计的序列对抗学习方法；具有在线自适应能力的自监督SLAM学习。</p>
  <b>个人简介：</b>
  <p>查红彬，北京大学信息科学技术学院智能科学系教授，机器感知与智能教育部重点实验室主任。主要从事计算机视觉与智能人机交互的研究，在三维视觉几何计算、三维重建与环境几何建模、三维物体识别等方面取得了一系列成果。出版学术期刊及国际会议论文300多篇，其中包括IEEE T-PAMI, IJCV, IEEE T-VCG, IEEE T-RA, IEEE T-SMC, ACM T-IST, JMLR, PR等国际期刊以及ICCV, ECCV, CVPR, CHI, ICML, AAAI, ICRA等国际学术会议论文100余篇。</p>
