var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"report"},[_c('h1',[_vm._v("文化产业与推广报告")]),_c('hr'),_c('b',[_vm._v("特邀讲者：陆地，北京大学")]),_c('br'),_c('br'),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"display":"inline-block","height":"auto","max-width":"100%"},attrs:{"src":require("@/assets/images/4/41.png")}})]),_c('br'),_c('b',[_vm._v("报告主题：视频满意度评估与大数据分析")]),_c('br'),_c('b',[_vm._v("时间地点：")]),_vm._v("2020年10月24日，周六，14:30-15:00，科艺中心会议室4。"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("传统上，视频节目的评估主要依靠收视率调查和点击量或下载量。2010年起，北京大学视听传播研究中心结合大数据技术和语义分析技术，创立了一套以满意度为核心指标、以新媒体用户为调查对象、定量研究与定性研究相结合的视频内容价值评估体系，连续进行了九次中国电视满意度调查、七次中国网络视频满意度调查和三次中国网络视频精品满意度调查暨发布，有力地纠正了节目评估“唯收视率”的弊端，为中国视频内容市场的多维度评估和市场调查方法的与时俱进以及视频产业的健康发展，提供了更多的选择，取得了良好的社会效益，获得了视频行业的广泛认可。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("陆地，中国人民大学新闻学博士，中国首位新闻传播学博士后（复旦大学）。曾任中国青年报、北京电视台等新闻媒体的记者、主任编辑和清华大学新闻与传播学院教授。现任北京大学新闻与传播学院教授、博士生导师，北京大学视听传播研究中心主任，兼任中国农工民主党中央文化委员会副主任、中国大众文化学会节庆礼仪研究会会长、中广联广播电视研究会副会长、北京市写作学会会长。1998年以来，发表广播电视与新媒体以及文化产业等方面的论文350余篇，出版专著、编著、译著等19部；2004年被中国广播电视协会评选为全国“十佳”广播电视理论工作者，2019年受聘“巴渝学者”。国家社科基金重大项目《周边传播理论与应用研究》首席专家")]),_c('hr'),_c('br'),_c('br'),_c('br'),_c('hr'),_c('b',[_vm._v("特邀讲者：陈学会，中国动漫集团")]),_c('br'),_c('br'),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"display":"inline-block","height":"auto","max-width":"100%"},attrs:{"src":require("@/assets/images/4/42.png")}})]),_c('br'),_c('b',[_vm._v("报告主题：数字文化产业发展理论与实践探索")]),_c('br'),_c('b',[_vm._v("时间地点：")]),_vm._v("2020年10月24日，周六，15:00-15:30，科艺中心会议室4。"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("数字创意产业成为国家战略性新兴产业，而其核心的数字文化产业日益受到政府和市场的高度关注，成为社会经济发展的新引擎、新动力。数字技术在文化产业的应用以出乎人们预料的效果，助力文化产业的快速、高效发展。数字技术与文化创作的天然耦合性，使得文化产品的创作不仅富产而且高产同时也拉平了一般创作生产的差距，并极大地提高了文化传播效率，推动了文化产业的快速发展。数字技术、特别是文化计算、大数据、人工智能、新媒体等新技术在文化产业的广泛、深入运用，导致传统文化产业理论和实践的不适宜，文化产业政策指导性失灵。因此，就必须在新技术与文化融合、即“文化+科技”条件下，对文化产业发展理论进行探索和创新，才能制定适应市场需要的政策，才能指导市场主体实践突破，才能有效促进文化产业健康、快速、高质量发展。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("陈学会，南开大学经济学博士，高级经济师、研究员。现任中国动漫集团有限公司党委委员、董事、副总经理。曾任中央企业、大型国有企业、上市公司、投资公司等高管职务。中国大众文化学会文化旅游专业委员会会长，北京市服务贸易协会副理事长。被聘为北京大学视听传播研究中心，南开大学滨海开发研究院，北京邮电大学世纪学院、北京电影学院动画学研究院、西南政法大学新媒体研究院、首都经贸大学会计学院等研究员、客座教授、学术委员，硕士生导师。被聘为国家艺术基金评审专家、科技部科技项目评审专家、文化和旅游部项目评审专家。中国动漫“新光奖”、中国国际新媒体短片节等终评委。")]),_c('p',[_vm._v("主导创作出品的原创3D动画片《彩虹宝宝》，已在70余个国家和地区电视台及腾讯、爱奇艺等互联网平台播出。作为主编、策划、制片人、监制等打造了《中国动漫春节联欢晚会》、《二十四节气》绘本、《五行传奇》、《龙行天下》、《菩提树》、《少年老子》、《二十节气密码》、《丝路游记》等动画、游戏、漫画产品。主导举办四届《中国数字艺术产业高峰论坛》。")]),_c('hr'),_c('br'),_c('br'),_c('br'),_c('hr'),_c('b',[_vm._v("特邀讲者：汪大海，北京师范大学")]),_c('br'),_c('br'),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"display":"inline-block","height":"auto","max-width":"100%"},attrs:{"src":require("@/assets/images/4/43.png")}})]),_c('br'),_c('b',[_vm._v("报告主题：周期汉字：汉字学习与汉字计算的革命")]),_c('br'),_c('b',[_vm._v("时间地点：")]),_vm._v("2020年10月24日，周六，15:50-16:20，科艺中心会议室4。"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("以汉字周期表和汉字科学编码与解码等原始创新底层技术为基础，围绕“新汉语：语文何以教育；新计算：汉字何以计算”，促进汉语言学 、信息科学、脑与认知科学、教育学等相关学科交叉研究与技术创新，牵头制定首个周期汉字科学学习国际标准和首个周期汉字计算国际技术标准，打造周期汉字创新链和产业链，催生具有中国特色、中国风格、中国气派的下一代汉字计算关键共性技术。最终实现“文化自信从汉字自信开始，汉字自信从汉字计算出发”的愿景。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("汪大海，北京师范大学二级教授、博士生导师，北京师范大学汉字科学研究中心主任。作为项目负责人先后主持国家哲学社会科学基金重大项目、重点项目、欧盟/联合国开发计划署援助项目、教育部、民政部、文化部等项目30余项。出版著作、教材20余部，发表学术论文近百篇。多份研究报告获得李克强总理等国家和省部级领导的重要批示，相关政策建议被有关部委采用。")]),_c('hr'),_c('br'),_c('br'),_c('br'),_c('hr'),_c('b',[_vm._v("特邀讲者：才武杰，北京中科视维文化科技有限公司")]),_c('br'),_c('br'),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"display":"inline-block","height":"auto","max-width":"100%"},attrs:{"src":require("@/assets/images/4/44.png")}})]),_c('br'),_c('b',[_vm._v("报告主题：面向大众数字文旅消费基于5G的沉浸性交互光影夜游")]),_c('br'),_c('b',[_vm._v("时间地点：")]),_vm._v("2020年10月24日，周六，16:20-16:50，科艺中心会议室4"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("利用数字虚拟视觉结合智能人机交互，基于5G技术的一体化平台，打造可观、可游、可玩等沉浸性互动光影幻境，将所在地打造成文化与科技、视觉与交互、线上与线下、体验与消费多维度融合的全新夜游文旅地标与夜晚数字旅游消费目的地。在形成核心游览产品的同时，也带动区域夜间餐饮、零售、住宿等商业经济的提升。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("才武杰，北京中科视维文化科技有限公司总裁助理兼规划总监。第一批数字化教育的探索者。2012年加入团队，作为核心人员参与重点国家课题以及商业项目的规划工作。目前带领团队负责项目与产品的整体规划与设计，规划策划领域包括：特色小镇、产业园区建设与运营；科技文旅景园、创新性场馆、主题公园、智慧文旅平台等科技文旅项目建设与运营；数字艺术展等展览展示布展与运营；生命科学、远古生命以及传统文化等方面的科学可视化工程建设等等。落地案例包括2019年北京世园会“智慧世园”项目与“奇幻光影森林”项目、江西定南工业视觉基地等等。")]),_c('hr'),_c('br'),_c('br'),_c('br'),_c('hr'),_c('b',[_vm._v("特邀讲者：陶寰，复旦大学")]),_c('br'),_c('br'),_c('div',{staticStyle:{"text-align":"center"}},[_c('img',{staticStyle:{"display":"inline-block","height":"auto","max-width":"100%"},attrs:{"src":require("@/assets/images/4/45.png")}})]),_c('br'),_c('b',[_vm._v("报告主题：数字高程模型、路网与步行成本")]),_c('br'),_c('b',[_vm._v("时间地点：")]),_vm._v("2020年10月24日，周六，16:50-17:20，科艺中心会议室4。"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("语言的扩散与传播最终形成方言区域，其背后的动因是人与人交际的频度。在交际频度无法直接观察的情况下，语言向心力、自然地理条件以及交通线、交通工具就是最重要的考察和计算的参项。从微观视角上以江苏省宜兴市作为观察对象，利用地理上的高程模型、路网，设定一个5小时步行圈进行计算分析，以此解释宜兴方言的分布格局。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("陶寰，复旦大学中文系汉语言文字学博士。现为复旦大学中文系教授，副系主任，中国语言学会理事，全国汉语方言学会理事。")]),_c('p',[_vm._v("主要从事汉语方言学研究，涉及方言语音类型、方言共时变异和微观演变、方言接触和方言史、方言语法等。先后主持国家社科基金项目、教育部人文社会科学研究规划基金项目，出版著作《上海方言词典》《蛮话词典》《松江方言研究》，主编“吴语重点方言研究丛书”；主要论文有“吴语浊音声母的类型及其音系地位”“新描写主义与吴语的调查研究”“吴闽语云、匣母的读音和闽语全浊声母的清化”，等。")]),_c('hr'),_c('br'),_c('br'),_c('br')])}]

